<template>
  <section id="contact-form">
    <div class="content" :class="$mq">
      <div data-aos="fade-up" class="content-box" :class="$mq">
        <form @submit="onSubmit">

          <div class="section-title">
            <p>Contact Details</p>
          </div>

          <fieldset>
            <legend class="field-title">Full Name <span class="required">*</span></legend>
            <div class="description">I always like to know who I'm speaking with.</div>
            <div class="split">
              <label for="fname">
                <input v-model="contact.fname" class= "input-field" type="text" id="fname" name="fname">
                First Name
              </label>
              <label for="lname">
                <input v-model="contact.lname" class= "input-field" type="text" id="lname" name="lname">
                Last Name
              </label>
            </div>
          </fieldset>

          <fieldset>
            <label for="email" class="field-title">Email Address <span class="required">*</span>
              <input v-model="contact.email" class= "input-field" type="email" id="email" name="email">
            </label>
          </fieldset>

          <fieldset>
            <label for="phone" class="field-title">Phone Number
              <div class="description">Not required but sometimes it's easier to just connect directly.</div>
              <input v-model="contact.phone" class= "input-field" type="tel" id="phone" name="phone">
            </label>
          </fieldset>

          <div class="section-title">
            <p>Message Details</p>
          </div>

          <fieldset>
            <legend class="field-title">Subject <span class="required">*</span></legend>
            <div v-for="(option, i) in options" :key="i" class="option">
              <label>
                <input v-model="contact.subject" :value="option" type="checkbox">
                {{ option }}
              </label>
            </div>
          </fieldset>

          <fieldset>
            <label for="message" class="field-title">Message <span class="required">*</span>
              <div class="description">Just give me a quick rundown.</div>
              <textarea v-model="contact.message" class= "input-field" id="message" name="message" rows="5"></textarea>
            </label>
          </fieldset>

          <div class="button-wrap">
            <input class="button" type="submit" value="Send Message">
          </div>

        </form>
      </div>

      <div data-aos="fade-up" class="content-box" :class="$mq">
        <h2>Or reach out to me directly</h2>
        <h3>Call <a class="link" href="tel:+37122102245" rel="noopener">+371 22 10 22 45</a> Mon-Fri: 9am - 5pm EET.</h3>
      </div>

    </div>
  </section>
</template>

<script>
export default {
  name: 'ContactForm',

  data () {
    return {
      options: ['Complete Website Build (from ground up)', 'Cleanup and Polish (to an already existing website)', 'Web Development Consulting (I\'ll point you in the right direction)', 'Feature Request (a minimum of 3-hours of work)', 'Let\'s Talk First (sometimes it\'s better to just talk through your needs)'],
      contact: {
        fname: null,
        lname: null,
        email: null,
        phone: null,
        subject: [],
        message: null
      },
      isSending: false
    }
  },

  methods: {
    clearForm () {
      for (const field in this.contact) {
        this.contact[field] = ''
      }
    },

    onSubmit (evt) {
      evt.preventDefault()
      this.isSending = true

      setTimeout(() => {
        // Build for data
        const form = new FormData()
        for (const field in this.contact) {
          form.append(field, this.contact[field])
        }
        // Send form to server
        this.$http.post('/app.php', form).then((response) => {
          // console.log(response)
          this.clearForm()
          this.isSending = false
        }).catch((e) => {
          // console.log(e)
        })
      }, 1000)
    }
  }
}
</script>

